#toCopy:hover:after {
  content: attr(data-name);
  position: absolute;
  left: 0;
  bottom: 0;
  background: rgba(5,13,156,.55);
  color: #fff;
  text-align: center;
  font-family: cursive;
  font-size: 14px;
  padding: 3px 0;
  width: 100%;
  }
  .close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.3;
    }
    .close:hover {
    opacity: 1;
    }
    .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #333;
    }
    .close:before {
    transform: rotate(45deg);
    }
    .close:after {
    transform: rotate(-45deg);
    }